import React, { useState, useEffect } from 'react';
import { Card, Button, Badge, LoadingSpinner } from '../../../shared';
// import { useStore } from '@shared/store'; // Temporarily disabled
import { adminService } from '../services/adminService';
import './AdminDashboard.css';

const AdminDashboard = ({
  refreshInterval = 30000,
  showQuickActions = true,
  className = ''
}) => {
  const [stats, setStats] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [refreshTimer, setRefreshTimer] = useState(null);

  // Get user from localStorage for now
  const user = {
    name: localStorage.getItem('user_name') || 'Admin',
    role: localStorage.getItem('user_role') || 'admin'
  };

  useEffect(() => {
    loadDashboardData();

    if (refreshInterval > 0) {
      const timer = setInterval(loadDashboardData, refreshInterval);
      setRefreshTimer(timer);
      return () => clearInterval(timer);
    }
  }, [refreshInterval]);

  const loadDashboardData = async () => {
    try {
      setError(null);
      // Use mock data for now - replace with actual API call when backend is ready
      await new Promise(resolve => setTimeout(resolve, 500)); // Simulate API delay

      const mockData = {
        users: {
          total: 1247,
          active: 892,
          new: 23,
          growth: 12.5
        },
        courses: {
          total: 156,
          published: 142,
          draft: 14,
          growth: 8.3
        },
        revenue: {
          total: 45280,
          monthly: 12450,
          weekly: 3210,
          growth: 15.7
        },
        support: {
          tickets: 23,
          pending: 7,
          resolved: 16,
          avgResponseTime: 2.4
        },
        system: {
          uptime: 99.9,
          performance: 98.2,
          storage: 67.8,
          lastBackup: '2 hours ago'
        }
      };

      setStats(mockData);
    } catch (err) {
      setError('Failed to load dashboard data');
      console.error('Dashboard error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleQuickAction = async (action) => {
    try {
      switch (action) {
        case 'backup':
          await adminService.createBackup();
          // Show success notification
          break;
        case 'maintenance':
          await adminService.runMaintenance('cleanup');
          break;
        case 'refresh':
          setLoading(true);
          await loadDashboardData();
          break;
        default:
          break;
      }
    } catch (err) {
      setError(`Failed to execute ${action}`);
    }
  };

  const renderStatCard = (title, value, change, icon, color = 'primary') => (
    <Card className={`stat-card stat-card--${color}`}>
      <div className="stat-card-content">
        <div className="stat-icon">
          <span>{icon}</span>
        </div>
        <div className="stat-details">
          <h3 className="stat-value">{value}</h3>
          <p className="stat-title">{title}</p>
          {change !== undefined && (
            <div className={`stat-change ${change >= 0 ? 'positive' : 'negative'}`}>
              <span className="change-icon">
                {change >= 0 ? '‚Üó' : '‚Üò'}
              </span>
              <span className="change-value">
                {Math.abs(change)}%
              </span>
            </div>
          )}
        </div>
      </div>
    </Card>
  );

  const renderRecentActivity = () => (
    <Card className="recent-activity">
      <div className="card-header">
        <h3>Recent Activity</h3>
        <Button variant="text" size="sm" onClick={() => handleQuickAction('refresh')}>
          Refresh
        </Button>
      </div>
      <div className="activity-list">
        {stats?.recentActivity?.map((activity, index) => (
          <div key={index} className="activity-item">
            <div className="activity-icon">
              <span>{adminService.getStatusIcon(activity.type)}</span>
            </div>
            <div className="activity-content">
              <p className="activity-description">{activity.description}</p>
              <div className="activity-meta">
                <span className="activity-user">{activity.user}</span>
                <span className="activity-time">
                  {new Date(activity.timestamp).toLocaleString()}
                </span>
              </div>
            </div>
            <Badge variant={activity.severity || 'info'} size="sm">
              {activity.type}
            </Badge>
          </div>
        ))}
      </div>
    </Card>
  );

  const renderSystemHealth = () => (
    <Card className="system-health">
      <div className="card-header">
        <h3>System Health</h3>
        <Badge
          variant={stats?.systemHealth?.overall === 'healthy' ? 'success' : 'warning'}
        >
          {stats?.systemHealth?.overall || 'Unknown'}
        </Badge>
      </div>
      <div className="health-metrics">
        {stats?.systemHealth?.metrics?.map((metric, index) => (
          <div key={index} className="health-metric">
            <div className="metric-info">
              <span className="metric-name">{metric.name}</span>
              <span className="metric-value">{metric.value}</span>
            </div>
            <div className="metric-bar">
              <div
                className={`metric-fill metric-fill--${metric.status}`}
                style={{ width: `${metric.percentage}%` }}
              />
            </div>
          </div>
        ))}
      </div>
    </Card>
  );

  const renderQuickActions = () => (
    <Card className="quick-actions">
      <div className="card-header">
        <h3>Quick Actions</h3>
      </div>
      <div className="actions-grid">
        <Button
          variant="outline"
          onClick={() => handleQuickAction('backup')}
          className="action-button"
        >
          üóÑÔ∏è Create Backup
        </Button>
        <Button
          variant="outline"
          onClick={() => handleQuickAction('maintenance')}
          className="action-button"
        >
          üîß Run Maintenance
        </Button>
        <Button
          variant="outline"
          onClick={() => window.open('/admin/users', '_blank')}
          className="action-button"
        >
          üë• Manage Users
        </Button>
        <Button
          variant="outline"
          onClick={() => window.open('/admin/content', '_blank')}
          className="action-button"
        >
          üìù Review Content
        </Button>
        <Button
          variant="outline"
          onClick={() => window.open('/admin/analytics', '_blank')}
          className="action-button"
        >
          üìä View Analytics
        </Button>
        <Button
          variant="outline"
          onClick={() => window.open('/admin/settings', '_blank')}
          className="action-button"
        >
          ‚öôÔ∏è System Settings
        </Button>
      </div>
    </Card>
  );

  if (loading && !stats) {
    return (
      <div className="admin-dashboard loading">
        <LoadingSpinner size="large" />
        <p>Loading dashboard...</p>
      </div>
    );
  }

  if (error && !stats) {
    return (
      <div className="admin-dashboard error">
        <div className="error-content">
          <h3>Dashboard Error</h3>
          <p>{error}</p>
          <Button onClick={loadDashboardData}>
            Try Again
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className={`admin-dashboard ${className}`}>
      <div className="dashboard-header">
        <div className="header-content">
          <h1>Admin Dashboard</h1>
          <p>Welcome back, {user?.name || 'Administrator'}</p>
        </div>
        <div className="header-actions">
          <Button
            variant="outline"
            onClick={loadDashboardData}
            disabled={loading}
          >
            {loading ? '‚Üª' : 'üîÑ'} Refresh
          </Button>
        </div>
      </div>

      {error && (
        <div className="dashboard-error">
          <p>{error}</p>
          <Button variant="text" onClick={() => setError(null)}>
            Dismiss
          </Button>
        </div>
      )}

      <div className="dashboard-stats">
        {renderStatCard(
          'Total Users',
          stats?.users?.total || 0,
          stats?.users?.change,
          'üë•',
          'primary'
        )}
        {renderStatCard(
          'Active Courses',
          stats?.courses?.active || 0,
          stats?.courses?.change,
          'üìö',
          'success'
        )}
        {renderStatCard(
          'Revenue (30d)',
          adminService.formatCurrency(stats?.revenue?.total || 0),
          stats?.revenue?.change,
          'üí∞',
          'warning'
        )}
        {renderStatCard(
          'Support Tickets',
          stats?.support?.open || 0,
          stats?.support?.change,
          'üé´',
          'info'
        )}
      </div>

      <div className="dashboard-content">
        <div className="dashboard-main">
          {renderRecentActivity()}
          {renderSystemHealth()}
        </div>

        <div className="dashboard-sidebar">
          {showQuickActions && renderQuickActions()}

          <Card className="system-info">
            <div className="card-header">
              <h3>System Information</h3>
            </div>
            <div className="info-list">
              <div className="info-item">
                <span className="info-label">Platform Version:</span>
                <span className="info-value">{stats?.system?.version || 'Unknown'}</span>
              </div>
              <div className="info-item">
                <span className="info-label">Uptime:</span>
                <span className="info-value">{stats?.system?.uptime || 'Unknown'}</span>
              </div>
              <div className="info-item">
                <span className="info-label">Database:</span>
                <span className="info-value">{stats?.system?.database || 'Unknown'}</span>
              </div>
              <div className="info-item">
                <span className="info-label">Storage Used:</span>
                <span className="info-value">
                  {stats?.system?.storage ? adminService.formatBytes(stats.system.storage) : 'Unknown'}
                </span>
              </div>
            </div>
          </Card>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;